body {
  padding:10px 10px 10px 10px;
  margin:0 5% 0 5% ;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.body p {
  font-size: 20px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-style: italic;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


ul {
  margin: 0;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

li {
  float: left;
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover:not(.active) {
  background-color: #4CAF50;
}

.active {
  background-color: #4CAF50;
}



h1{
  color: #4CAF50;

}
.aboutme {

  padding: 10px;
  box-shadow: 5px 10px 8px 10px #888888;

  margin : 5% 15% 0 15%;
  padding : 15px 15px 15px 15px;
  text-align: justify;
  font-family: 'Times New Roman', Times, serif ;
}


img{
  width:200px;
  height:300px;
box-shadow:0px 0px 0px 1px #888888;
}






div.polaroid {
  float:none;
  width: 300px;
  height: 400px;;
  margin-top: 5%;
  padding: 20px 10px 20px 10px;
  border: 1px solid #BFBFBF;
  background-color: white;
  box-shadow: 10px 10px 5px #aaaaaa;
}

div.rotate_right {
  float: left;
  transform: rotate(7deg);
}

div.rotate_left {
  float: left;
  transform: rotate(-8deg);
}



.products{
  display: flexbox;
  column-count: 3;
  justify-content:space-around;

}
.product{
  width: 300px;
  height: 400px;
  font-size: 10px;
  font-style: normal;
  padding: 20px 10px 20px 10px;
  border: 1px solid #BFBFBF;
  background-color: white;
  box-shadow: 10px 10px 5px #aaaaaa;

}
.product h2{
  margin-top: 2px;
  margin-bottom:2px;
}


  .product p {
    margin-top: 2px;
    margin-bottom: 2px;
  }


h3{
  color:red;
  font-size: 18px;
  text-align: center;

}

  .cart{
    justify-content:space-around;
  
  }
  .cart-row{
    margin-top: 10px;
    font-size: 10px;
    font-style: normal;
    border: 1px solid #BFBFBF;
    background-color: white;
    box-shadow: 10px 10px 5px #aaaaaa;


    display: flex;
    justify-content: space-between;
    align-items: center;

  
  }
  .cart-row h2{
    padding: 2px 2px 2px 2px;
    margin: 2px 2px 2px 2px;
  }
  
  
    .cart-row p {
      margin-top: 2px;
      margin-bottom: 2px;
    }
  
.search{
  width:30px;
  height: 20px;
}


  .bottun{
    width: 300px;
    height: 50px;
    font-size: 16px;
    font-weight: bold;
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: azure;
    background-color: #4CAF50;
  }
  .bottunred{
    float: right;
    width: 200px;
    height: 50px;
    font-size: 16px;
    font-weight: bold;
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: azure;
    background-color: red;
  }
  .gotocard{
    color: white;
    padding: 14px 25px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    background-color: #4CAF50;
  }





  /* Registration*/
  .container {
    padding: 16px;
    background-color: white;
  }
  
  input[type=text], input[type=password] , textarea{
    width: 100%;
    padding: 15px;
    margin: 5px 0 10px 0;
    display: inline-block;
    border: none;
    background: #f1f1f1;
  }
  
  input[type=text]:focus, input[type=password]:focus {
    background-color: #ddd;
    outline: none;
  }

  .registerbtn {
    background-color: #4CAF50;
    font-size: 16px;
    color: white;
    padding: 16px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
    opacity: 0.9;
  }
  
  .registerbtn:hover {
    opacity: 1;
  }
  
  a {
    color: dodgerblue;
  }
  
